.app{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.app-date{
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.app-logo{
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
.app-user{
  height: 50px;
  width: auto;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  
}
.app-user-image{
  border: 1px solid rgb(224, 224, 224);
  height: 40px;
  width: 40px;
  border-radius: 100%;
  margin-right: 10px;
  background-position: center;
  background-size: contain;
  background-image: url("../imgs/default.jpg");
}
main{
  margin-top: 110px;
  margin-bottom: 50px;
  width: 100%;
  background-color: rgb(247, 247, 247);
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media(max-width:800px){
  main{
    margin-top: 40px;
  }
  
}
@media(max-width:600px){
  .app-user-image{
    height: 30px;
    width: 30px;
  }
  h3{
    font-size: 15px;
  }
}